.about-us{
  padding: 1em;
  font-size: 22px;
  text-align: center;
  font-family: 'Titillium Web';
  text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
  padding-top: 2em;
  padding-bottom: 2em;
}
.about-us img{
  width: 100%;
  max-width: 400px;
  border-radius: 5px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.6);
}
.home h3{
  color: #5d1917;
  font-family: 'Lobster';
  font-size: 8vw;
  font-weight: bolder;
  text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding: 1em;
  padding-top: 2em;
  margin: 0.5em;
}
.brendels{
  font-family: 'Allura';
  font-size: 35px;
  color: red;
  font-weight: bolder;
  padding: 0.2em;
}